@import '../../_variables.scss';

.productDetails {
  padding: 10px;
  text-align: right;
  &__badges {
    position: absolute;
    z-index: 5;
    margin-top: 40px;
  }
  &__col:first-child {
    direction: ltr;
  }
  &__originalClass {
    height: calc(100vh - 215px);
    padding-bottom: 50px !important;
  }

  &__originalClass .image-gallery-image {
    height: calc(100vh - 215px);
    max-height: -webkit-fill-available !important;
  }

  &__originalClass > video,
  &__originalClass > img {
    height: calc(100vh - 215px);
    max-height: -webkit-fill-available !important;
  }
  &__thumbnailClass {
    height: 140px;
    width: 80px;
  }
  &__price {
    color: #1b9ad1;
    margin: 0 10px;
    font-size: 29px;
    direction: rtl;
  }
  &__price > del {
    margin-left: 10px;
  }
  &__badgePrice {
    display: inline-block;
    color: white;
    background-color: $discountColor;
    border-radius: 4px;
    padding: 5px 10px;
    margin: 10px;
    line-height: 1.2;
    font-size: 12px;
  }
  &__color > div {
    font-size: 14px;
    font-weight: 700;
    margin: 25px 10px 0 25px;
    direction: rtl;
  }
  &__color > img {
    cursor: pointer;
    width: 60px;
    height: 60px;
    margin: 10px;
  }
  &__color > img:hover {
    opacity: 0.7;
  }
  &__selectedColor {
    border: 3px solid #337ab7;
  }
  &__size {
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 700;
    margin: 25px 10px;
    direction: rtl;
  }
  &__additionalInfo {
    direction: rtl;
    color: #525252;
    background-color: #f3f3f6;
    border: 1px solid #d9d9d9;
    border-radius: 5px;
    padding: 9px 30px;
    font-size: 15px;

    > div {
      color: black;
      font-weight: 700;
    }
    > li {
      margin-top: 15px;
    }
  }
}

.youMayAlsoLike {
  &__body {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 50%;
    margin: 30px 0;
  }
}

.productNotFound {
  padding: 20px;
  text-align: center;
  font-size: 30px;
}

.image-gallery-left-nav .image-gallery-svg,
.image-gallery-right-nav .image-gallery-svg {
  height: 50px;
  width: 50px;
}

.redColor {
  color: red;
}

@media screen and (min-width: 760px) {
  .productDetails {
    &__cols {
      display: flex;
    }
    &__col {
      width: 50%;
    }
    &__col:last-child {
      margin: 0 15px;
    }
  }

  .youMayAlsoLike {
    &__item {
      width: 25%;
    }
  }
}
