.header {
  padding: 5px;
  height: 110px;
  align-items: center;
  text-align: start;
  display: flex;
  &__logo {
    width: 60%;
    img {
      width: 120px;
      height: 120px;
    }
  }
}

/*display desktop when the browser window is 450px width or more*/
@media screen and (min-width: 450px) {
  .header {
    &__logo {
      img {
        width: 140px;
      }
    }
  }
}
