@import '../../_variables.scss';

.followUsOn {
  position: relative;
  margin: 50px auto;
  > img {
    width: 100vw;
  }
  &__button {
    position: absolute;
    top: 20px;
    padding: 10px;
    font-weight: 800;
    font-size: 13px;
    border-radius: 25px;
  }
  &__snapchat {
    left: 5px;
    border: 2px solid black;
    color: black;
  }
  &__whatsapp {
    right: 5px;
    background-color: $whatsappColor;
    color: white;
  }
}

@media screen and (min-width: 450px) {
  .followUsOn {
    > img {
      width: 50vw;
      display: flex;
      margin: 0 auto;
    }
    &__button {
      font-size: 20px;
    }
    &__snapchat {
      left: 10%;
    }
    &__whatsapp {
      right: 10%;
    }
  }
}
