@import '../../_variables.scss';
.productCard {
  &__badges {
    position: absolute;
    z-index: 5;
  }
  &__item {
    padding: 0 10px;
    cursor: pointer;
    text-align: center !important;
    &__mainImage {
      overflow: hidden;
    }
    &__mainImage > img {
      width: 100%;
      height: 160px;
      border-radius: 5px;
      transition: transform 0.9s ease-in-out; /* Smooth transition effect */
    }
    &__mainImage:hover > img {
      transform: scale(1.2); /* Zoom in*/
    }
    &__discretion {
      text-align: center !important;
    }
    &__discretion:hover {
      color: $hoverColor;
    }
    &__price {
      color: #696969;
      margin: 0 10px;
      direction: rtl;
    }
    &__price > del {
      margin-left: 10px;
    }
  }
}

.redColor {
  color: red;
}

.noDecoration {
  text-decoration: none;
  color: inherit;
}

.circularBadge {
  margin-top: 5px;
  margin-left: 5px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: $newColor;
  display: flex;
  justify-content: center;
  > span {
    font-size: 12px;
    color: white;
    margin-top: 8px;
  }
}

.discountColor {
  background-color: $discountColor;
}

@media screen and (min-width: 760px) {
  .productCard {
    &__item {
      &__mainImage > img {
        height: 250px;
      }
    }
  }
}
