@import '../../_variables.scss';
.mainSlider {
  position: relative;
  &__img {
    overflow: hidden;
  }
  &__img > img {
    width: 100%;
    height: calc(50vh - 110px);
    transition: transform 0.9s ease-in-out;
  }

  &__img:hover > img {
    transform: scale(1.1);
  }

  &__title {
    position: absolute;
    color: black;
    top: 0;
    left: calc(90% - 150px);
    // color: white;
    // top: 40px;
    // left: 50px;
    > p {
      font-size: 20px;
      font-weight: 700;
    }
    > a > button {
      background-color: transparent;
      border: 2px solid black;
      font-weight: 700;
      border-radius: 8px;
      width: 150px;
      height: 30px;
      cursor: pointer;
      font-size: 20px;
      transition: background-color 1s ease;
    }
    > a > button:hover {
      background-color: black;
      color: white;
    }
  }
}

@media screen and (min-width: 450px) {
  .mainSlider {
    &__img > img {
      width: 100%;
      height: calc(100vh - 110px);
    }

    &__title {
      // top: calc(50% - 40px);
      // left: calc(70% - 200px);
      top: 0;
      left: calc(85% - 200px);
      > p {
        font-size: 35px;
      }
      > button {
        width: 200px;
        height: 40px;
        font-size: 25px;
      }
    }
  }
}
