@import '../../_variables.scss';
.productNav {
  &__nav {
    display: flex;
    flex-wrap: wrap;
    background: #f6f6f6;
    margin-bottom: 10px;
    padding: 18px 10px;
    font-size: 13px;
    font-weight: 700;
    > a {
      color: #222222;
      text-decoration: none;
      transition: color 0.5s ease-in-out;
    }
    > a:hover {
      color: $hoverColor;
    }
    &__break::before {
      content: '>';
      margin: 0 5px;
    }
    &__description {
      opacity: 0.5;
    }
  }
}
