@import '../../variables';

.categoryProducts {
  padding: 0 10px;

  &__body {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 50%;
    margin: 30px 0;
  }
  &__loadMore {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 25px auto;
  }
  &__loadMore > button {
    width: 200px;
    height: 40px;
    border: 2px solid $loadMoreColor;
    transition: background-colo 0.3s, all 0.3s;
    border-radius: 60px;
    font-size: 20px;
    background-color: transparent;
  }

  &__loadMore > button:hover {
    background-color: $loadMoreColor;
  }
}

.categoryNotFound {
  padding: 20px;
  text-align: center;
  font-size: 30px;
}

@media screen and (min-width: 450px) {
  .categoryProducts {
    &__item {
      width: 25%;
    }
    &__loadMore > button {
      width: 300px;
    }
  }
}
