.footer {
  padding: 50px 20px 65px 20px;
  background-color: #383838;
  &__main_title {
    color: rgb(255, 255, 255);
    font-size: 16px;
    margin-bottom: 30px;
  }
  &__discretion {
    color: #878787;
    margin-bottom: 25px;
  }

  &__phone {
    > img {
      width: 24px;
      height: 24px;
      margin-left: 5px;
      vertical-align: middle;
    }
    > span {
      color: white;
    }
    margin-bottom: 25px;
  }

  &__follow_us_on {
    color: white;
    > a > img {
      margin: 5px;
      width: 30px;
      height: 30px;
    }
  }
  &__links > div {
    color: #878787;
    text-decoration: none;
    margin-bottom: 10px;
    cursor: pointer;
  }
  &__links > div:hover {
    color: #c39f71;
  }
  &__whatsapp_link {
    position: fixed;
    bottom: 10px;
    left: 16px;
    z-index: 1000000;
    width: 50px;
    height: 50px;
    cursor: pointer;
  }
}

@media screen and (min-width: 450px) {
  .footer {
    padding: 50px 70px;
    display: flex;
    &__col {
      width: 50%;
      padding: 0 20px;
    }
  }
}
